@import "../../styles/utils/vars";

hr.divide-block {
    margin: 5px 0;
    margin-left: 0;
}

.week-number {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 5px;

    &__btn-add-route {
        margin-right: auto;
        background-color: green;
        border-radius: 4px;
        // z-index: 3;
        padding: 5px;
        &:hover {
            background-color: rgb(20, 179, 20);
        }
        & a {
            color: white;
            text-decoration: none;
        }
    }
    &__btn-show-driver {
        margin-left: auto;
        background-color: rgb(232, 86, 198);
        border-radius: 4px;
        // z-index: 3;
        padding: 5px;
        color: white;
        &:hover {
            background-color: rgb(232, 86, 198, 0.8);
            cursor: pointer;
        }
    }

    &__container {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 16px;
    }

    &__btn {
        border: none;
        background-color: $colorGray;
        color: #fff;
        height: 30px;
        width: 30px;
        border-radius: 4px;
        &:hover {
            cursor: pointer;
            background-color: $headerColor;
        }
    }
}

.week-header {
    &__row {
        display: flex;
        flex-direction: row;
        background-color: rgb(140, 177, 186);
        border-radius: 4px;
        position: sticky;
        top: 0;
        z-index: 1;
    }
    &__day-container {
        width: 100%;
        padding: 5px 5px;
        margin: 2px 2px;
        border-radius: 4px;
        transition: background-color 0.5s ease;
        &:hover {
            background-color: $headerColor;
            cursor: pointer;
        }
        &_date-item {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            text-align: center;
            padding: 0px 4px;
            background-color: $colorGray;
            border-radius: 4px;
            color: #fff;
            width: 100%;
            height: 100%;
            text-transform: uppercase;
            &__name {
                margin-top: 5px;
            }
            &__date {
                margin-bottom: 5px;
            }
        }
        &_truck {
            margin: 5px;
            // width: 100%;
        }
    }
}

