.week-truck {
    position: relative;
    z-index: 0;

    &__row {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
        background-color: rgb(233, 228, 185);
        border-radius: 4px;
        margin: 2px 1px 2px 1px;
        transition: background-color 0.5s ease;
        &:hover {
            background-color: rgb(142, 140, 115, 0.4);
            cursor: pointer;
        }
    }
    &__day-container {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        padding: 4px 5px;
        margin: 0 2px;
        text-align: center;
    }

    &__first-col {
        display: flex;
        flex-direction: column;
        // justify-content: center;
        color: blue;
        height: 100%;
        width: 100%;
        padding: 4px 4px;
        background-color: rgba(169, 197, 133, 0.6);
        color: rgb(255, 255, 255);
        border-radius: 4px;
        gap: 2px;
        font-size: 14px;
    }
    &__truck-plates {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 4px 4px;
        text-align: center;
        background-color: rgb(58, 138, 32, 0.7);
        border-radius: 4px;
        &_icon {
            transform: scale(1.3);
        }
    }
    &__trailer-plates {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        padding: 4px 4px;
        text-align: center;
        background-color: rgb(58, 138, 32, 0.7);
        border-radius: 4px;
        &_icon {
            transform: scale(1.3);
        }
    }
    &__driver-details {
        padding: 4px 4px;
        text-align: center;
        background-color: rgb(58, 138, 32, 0.7);
        border-radius: 4px;
        color: #fff;
        &_phone-number {
            font-size: 14px;
            color: black;
        }
    }
}

.planner-button {
    // background-color: #d7f0ed;
    width: 75px;
    color: green;
    border-radius: 5px;
    // opacity: 0.3;
    transition: all 0.5s;
    border: none;
    padding: 1px;
    margin: 5px;
    text-align: center;
    background-color: rgb(233, 228, 185);
    &:hover {
        opacity: 0.6;
        padding: 1px;
        margin: 5px;
        text-align: center;
        vertical-align: center;
        width: 75px;
        border-radius: 5px;
        color: rgb(8, 210, 8);
        background-color: $headerColor;
        transition: all 0.5s;
        cursor: pointer;
    }
}
