.top-section-header {
    display: flex;
    justify-content: center;
    font-size: 25px;
    color: white;
    border-radius: 4px;
    height: 38px;
    width: 50px;
    align-items: center;
    &:hover {
        cursor: pointer;
        background-color: rgba(122, 140, 138, 0.48);
        transition: background-color 0.5s ease;
    }
}

.header-navbar {
    display: flex;
    align-items: center;
    height: 40px;
    padding: 0 10px 0 0px;

    &__title {
        font-size: 20px;
        color: #fff;
        margin-left: 30px;
        padding: 10px;
        border-radius: 4px;
        a {
            text-decoration: none;
            color: #fff;
            &:hover {
                color: rgb(158, 172, 173);
                transition: background-color 0.5s ease;
            }
        }
        &:hover {
            // background-color: rgb(158, 172, 173);
            cursor: pointer;
            transition: background-color 0.5s ease;
        }
    }
    &__user {
        margin-left: auto;
        margin-right: 50px;
        padding: 10px;
        border-radius: 4px;
        a {
            font-size: 14px;
            text-decoration: none;
            color: #fff;
            &:hover {
                color: rgb(158, 172, 173);
                transition: background-color 0.5s ease;
            }
        }
        &:hover {
            cursor: pointer;
            transition: background-color 0.5s ease;
        }
    }
    &__login {
        margin-left: auto;
        margin-right: 50px;
        padding: 10px;
        border-radius: 4px;
        a {
            font-size: 14px;
            text-decoration: none;
            color: #fff;
            &:hover {
                color: rgb(158, 172, 173);
                transition: background-color 0.5s ease;
            }
        }
        &:hover {
            cursor: pointer;
            transition: background-color 0.5s ease;
        }
    }
}
