@import "../../styles/utils/vars";

.orders-table {
    border-collapse: separate;
    border-spacing: 0 3px;
    padding: 0 5px;
    width: 100%;

    &__header {
        position: sticky;
        top: 2px;
        z-index: 2;

        &-row {
            position: relative;
            top: -3px;
            height: 50px;
            text-align: center;
            text-transform: uppercase;
            font-size: 14px;
            box-shadow: 0 1px 1px rgba(0, 0, 0, 0.14),
                0 2px 2px rgba(0, 0, 0, 0.12);
        }
        &-th {
            border-right: 0.5px solid gray;
            color: white;
            background-color: $colorGray;
            font-weight: bold;
            font-size: 0.8em;

            &:first-child {
                border-top-left-radius: 4px;
                border-bottom-left-radius: 4px;
            }
            &:last-child {
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;
            }
        }
    }
    &-td {
        border-radius: 4px;
        overflow: hidden;
    }

    &__body {
        &-row {
            font-size: 14px;
            background-color: #fff;
            input {
                cursor: pointer;
                transform: scale(130%);
            }
            &:last-child {
                box-shadow: 0 8px 10px rgba(0, 0, 0, 0.14);
            }

            &:hover {
                box-shadow: 0 8px 10px rgba(0, 0, 0, 0.14),
                    0 3px 14px rgba(0, 0, 0, 0.12), 0 5px 5px rgba(0, 0, 0, 0.2);
                position: relative;
            }
        }
        &-td {
            padding: 3px;
            text-align: center;
            border-right: 0.5px solid gray;
            white-space: nowrap;
            font-size: 12px;

            &:first-child {
                border-top-left-radius: 4px;
                border-bottom-left-radius: 4px;
            }
            &:last-child {
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;
                border-right: 0;
            }
        }
    }
}

.orders-header-block {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &__buttons-container {
        display: flex;
        gap: 5px;
    }

    &__add-order-btn {
        background-color: green;
        border-radius: 4px;
        z-index: 3;
        padding: 5px;
        color: white;
        border: none;
        &:hover {
            background-color: rgb(20, 179, 20);
            cursor: pointer;
        }
    }
}
